/* The Modal (background) */
.modal {
  position: fixed;
  /* Stay in place */
  padding-top: 100px;
  /* Location of the box */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: rgb(0, 0, 0);
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.9);
  /* Black w/ opacity */
}

.modal-language{
  text-align: center;
}

.modal-section {
  margin: auto;
  display: block;
  margin-bottom: 40px;
  margin-top: -40px;
}

.modal-body,
.modal-content {
  background-color: rgba(0, 0, 0, 0);
  color: white;
}

/* Add Animation - Zoom in the Modal */
.modal-content {
  -webkit-animation-name: zoom;
          animation-name: zoom;
  -webkit-animation-duration: 0.6s;
          animation-duration: 0.6s;
}

.modal-content i {
  font-size: 450px;
}

#node {
  font-size: 400px;
}

.modal-body ul li {
  list-style-type: none;
  list-style: none;
}

