#button-send i {
  display: inline-block;
  padding-left: 10px;
  position: absolute;
  color: white;
  line-height: inherit;
  text-align: inherit;
  font-size: inherit;
  overflow: hidden;
  -webkit-transform-origin: center;
          transform-origin: center;
  transition: all 1s ease-in-out;
}
/* Animations */
@-webkit-keyframes send {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  12.5% {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    border-radius: 50%;
    background-color: transparent;
    opacity: 0.9;
  }
  25% {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    border-radius: 50%;
    background-color: transparent;
    right: -3rem;
    opacity: 0.8;
  }
  37.5% {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    border-radius: 50%;
    background-color: transparent;
    right: -3.5rem;
    opacity: 0.7;
  }
  50% {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    border-radius: 50%;
    background-color: transparent;
    right: -4rem;
    opacity: 0.6;
  }
  62.5% {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    border-radius: 50%;
    background-color: transparent;
    right: -4.5rem;
    opacity: 0.5;
  }
  75% {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    border-radius: 50%;
    background-color: transparent;
    right: -5rem;
    opacity: 0.5;
  }
  87.5% {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    background-color: transparent;
    right: -5.5rem;
    opacity: 0.4;
  }
  100% {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    background-color: transparent;
    right: -7rem;
    opacity: 0.3;
  }
}
